a {
  text-decoration: none;
  color: #E0ECFF;
}
.governance-box {
  border-top: 1px solid #203B60;
  color: #FFF;
  font-weight: 500;
}
.governance-box .governance-box-info {
  background-image: url('./img/governance-bg.svg');
  background-size: 100%;
  width: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.governance-box .governance-box-info .governance-title {
  margin-top: 76px;
  font-size: 32px;
  font-weight: 500;
  letter-spacing: 5px;
  text-align: center;
}
.governance-box .governance-box-info .governance-des {
  width: 695px;
  font-size: 20px;
  line-height: 24px;
  padding-top: 34px;
  color: #E0ECFF;
  font-weight: normal;
  text-align: center;
}
.governance-box .governance-box-info .active-pool-votes-num,
.governance-box .governance-box-info .active-esectutive-votes-num {
  box-sizing: content-box;
  padding: 8px;
  font-size: 14px;
  background-color: #0D1D52;
  border: 1px solid #203B60;
  border-radius: 4px;
  margin-top: 24px;
  font-weight: 600;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.governance-box .governance-box-info .active-pool-votes-num .active-num,
.governance-box .governance-box-info .active-esectutive-votes-num .active-num,
.governance-box .governance-box-info .active-pool-votes-num .no-active-num,
.governance-box .governance-box-info .active-esectutive-votes-num .no-active-num {
  width: 22px;
  height: 22px;
  color: #59AE99;
  border-radius: 50%;
  font-size: 14px;
  font-weight: 500;
  border: 2px solid #59AE99;
  display: flex;
  font-weight: 600;
  justify-content: center;
  align-items: center;
  margin-right: 8px;
}
.governance-box .governance-box-info .active-pool-votes-num .no-active-num,
.governance-box .governance-box-info .active-esectutive-votes-num .no-active-num {
  color: #FFF;
  border: 2px solid #FFF;
}
.governance-box .governance-box-info .active-pool-votes-num {
  margin-top: 42px;
}
.governance-box .governance-box-info .active-pool-votes-num:hover,
.governance-box .governance-box-info .active-esectutive-votes-num:hover {
  background-image: url('./img/card-bg.svg');
  border: 1px solid #cd7a37;
}
.governance-box .governance-box-info .down {
  margin-top: 51px;
}
.governance-box .governance-box-info .how-to-participate {
  font-size: 28px;
  font-weight: 500;
  letter-spacing: 5px;
  margin-top: 74px;
}
.governance-box .governance-box-info .participate-step {
  margin-top: 68px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1100px;
}
.governance-box .governance-box-info .participate-step .step {
  width: 336px;
  height: 128px;
  background: #112247;
  border: 1px solid #203B60;
  border-radius: 4px;
  text-align: center;
  cursor: pointer;
}
.governance-box .governance-box-info .participate-step .step .hover-go {
  text-align: right;
  height: 31px;
  padding: 12px 12px 0 0;
  cursor: pointer;
}
.governance-box .governance-box-info .participate-step .step .step-logo {
  height: 37px;
}
.governance-box .governance-box-info .participate-step .step .step-text {
  font-size: 24px;
  color: #E0ECFF;
  margin-top: 6px;
}
.governance-box .governance-box-info .participate-step .step .hover {
  box-sizing: content-box;
  padding: 16px 24px;
  background: #112247;
  border: 1px solid #647291;
  border-radius: 16px;
  margin-top: 43px;
  text-align: left;
  position: relative;
  display: none;
}
.governance-box .governance-box-info .participate-step .step .hover .san {
  position: absolute;
  top: -14px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-width: 0 14px 14px;
  border-style: solid;
  border-color: transparent transparent #647291;
}
.governance-box .governance-box-info .participate-step .step .hover .san:after {
  content: "";
  position: absolute;
  top: 1px;
  left: -13px;
  border-width: 0 13px 13px;
  border-style: solid;
  border-color: transparent transparent #112247;
}
.governance-box .governance-box-info .participate-step .step .hover-go img {
  display: none;
}
.governance-box .governance-box-info .participate-step .step:hover .hover {
  display: block;
}
.governance-box .governance-box-info .participate-step .step:hover .hover-go img {
  display: inline-block;
}
.governance-box .governance-box-info .participate-step .step:hover {
  background-image: url('./img/card-bg.svg');
  background-size: 100%;
  border: 1px solid #CD7A37;
}
.governance-box .governance-box-info .executive-votes {
  margin-top: 233px;
  font-size: 28px;
  font-weight: 500;
  letter-spacing: 5px;
}
.governance-box .governance-box-info .dip-list {
  margin-top: 50px;
  width: 1100px;
  height: 100%;
  margin-bottom: 58px;
}
.governance-box .governance-box-info .dip-list .in-progress-executed {
  display: flex;
  align-items: center;
  justify-content: end;
}
.governance-box .governance-box-info .dip-list .in-progress-executed .in-progress,
.governance-box .governance-box-info .dip-list .in-progress-executed .executed {
  width: 176px;
  height: 48px;
  background: #112247;
  border: 1px solid #203B60;
  border-radius: 8px;
  line-height: 48px;
  padding: 0 24px;
  cursor: pointer;
}
.governance-box .governance-box-info .dip-list .in-progress-executed .in-progress {
  margin-right: 14px;
}
.governance-box .governance-box-info .dip-list .in-progress-executed .check {
  background: #5577FD;
  border-radius: 8px;
  border: 1px solid transparent;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.governance-box .governance-box-info .dip-list .in-progress-executed img {
  width: 13.9px;
  height: 9px;
}
.governance-box .governance-box-info .dip-list .dip-list-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  height: 100%;
}
.governance-box .governance-box-info .dip-list .dip-list-box .dip-list-box-info {
  width: 508px;
  height: 206px;
  background: #112247;
  border: 1px solid transparent;
  border-radius: 8px;
  padding: 8px 16px;
  margin-top: 48px;
}
.governance-box .governance-box-info .dip-list .dip-list-box .dip-list-box-info .dip-list-title {
  color: #93A1B8;
  font-size: 14px;
  display: flex;
  justify-content: space-between;
}
.governance-box .governance-box-info .dip-list .dip-list-box .dip-list-box-info .dip-list-title .dip-executed,
.governance-box .governance-box-info .dip-list .dip-list-box .dip-list-box-info .dip-list-title .dip-progress {
  display: flex;
  align-items: center;
}
.governance-box .governance-box-info .dip-list .dip-list-box .dip-list-box-info .dip-list-title .dip-executed .executed-bg,
.governance-box .governance-box-info .dip-list .dip-list-box .dip-list-box-info .dip-list-title .dip-progress .executed-bg {
  width: 12px;
  height: 12px;
  background-color: #CD7A37;
  border-radius: 50%;
  margin-right: 8px;
}
.governance-box .governance-box-info .dip-list .dip-list-box .dip-list-box-info .dip-list-title .dip-executed .progress-bg,
.governance-box .governance-box-info .dip-list .dip-list-box .dip-list-box-info .dip-list-title .dip-progress .progress-bg {
  width: 12px;
  height: 12px;
  background-color: #5577FD;
  border-radius: 50%;
  margin-right: 8px;
}
.governance-box .governance-box-info .dip-list .dip-list-box .dip-list-box-info .dip-list-des {
  margin-top: 32px;
  width: 449px;
  height: 36px;
  display: flex;
  align-items: center;
  font-size: 18px;
  color: #E0ECFF;
}
.governance-box .governance-box-info .dip-list .dip-list-box .dip-list-box-info .leading-option {
  margin-top: 32px;
  font-size: 14px;
  height: 36px;
  display: flex;
  align-items: center;
}
.governance-box .governance-box-info .dip-list .dip-list-box .dip-list-box-info .leading-option .winner-option {
  color: #59AE99;
}
.governance-box .governance-box-info .dip-list .dip-list-box .dip-list-box-info .dip-time {
  font-size: 12px;
  color: #52617F;
  margin-top: 16px;
}
.governance-box .governance-box-info .dip-list .dip-list-box .dip-list-box-info:hover {
  border: 1px solid #CD7A37;
}
@media only screen and (min-width: 770px) and (max-width: 1140px) {
  .governance-box .governance-box-info .participate-step {
    width: 760px;
  }
  .governance-box .governance-box-info .participate-step .step {
    width: 250px;
  }
  .governance-box .governance-box-info .dip-list {
    width: 760px;
  }
  .governance-box .governance-box-info .dip-list .dip-list-box .dip-list-box-info {
    width: 370px;
  }
  .governance-box .governance-box-info .dip-list .dip-list-box .dip-list-box-info .dip-list-des {
    width: 336px;
  }
}
@media only screen and (max-width: 770px) {
  .governance-box .governance-box-info .governance-des {
    width: 326px;
  }
  .governance-box .governance-box-info .participate-step {
    width: 326px;
    flex-wrap: wrap;
  }
  .governance-box .governance-box-info .participate-step .step {
    width: 326px;
    margin-top: 24px;
  }
  .governance-box .governance-box-info .dip-list {
    width: 326px;
    height: 100%;
  }
  .governance-box .governance-box-info .dip-list .dip-list-box {
    flex-wrap: wrap;
    height: 100%;
  }
  .governance-box .governance-box-info .dip-list .dip-list-box .dip-list-box-info {
    width: 326px;
    margin-top: 24px;
  }
  .governance-box .governance-box-info .dip-list .dip-list-box .dip-list-box-info .dip-list-des {
    width: 326px;
  }
}
